html {
  padding-top: 60px;
  background-color: #adcbe3;
}

header {
  position: fixed;
  top: 0;
  padding: 5px 0;
  z-index: 10;
  background-color: #63ace5;
  vertical-align: middle;
  width: 100%;
  margin-bottom: 100px;
}

header div {
  display: inline-block;
  height: 40px;
}

header .logo {
  width: 18%;
  padding-left: 2%;
}

header .logo img {
  width: 40px;
}

header .search {
  width: 60%;
}

header .search img {
  width: 40px;
}

header .hamburger {
  width: 40px;
  padding-right: 2%;
  text-align: right;
  width: 40px;
}

header .hamburger img {
  width: 40px;
}

html {
  padding-bottom: 80px;
}

footer {
  position: fixed;
  display: block;
  padding: 5px 0;
  height: 50px;
  width: 100%;
  left: 0px;
  bottom: 0px;
  background-color: #63ace5;
}

footer div {
  vertical-align: middle;
  display: inline-block;
}

footer div a i {
  color: #2a4d69;
}

footer .footerContainer {
  width: 100%;
}

footer .userControls {
  width: 28%;
  padding-left: 2%;
}

footer .userControls a {
  margin: 10px 0;
}

footer .socialMediaLinks {
  width: 68%;
  text-align: right;
}

footer .socialMediaLinks a {
  padding-right: 2%;
}

#navigation {
  width: 95%;
  display: none;
  margin: 0 auto;
}

#navigation nav {
  margin-top: -60px;
  width: 100%;
}

#navigation nav a {
  width: 40px;
  display: block;
}

#navigation nav a img {
  width: 100%;
}

#navigation nav ul {
  display: block;
  background-color: #adcbe3;
  padding: 0;
  list-style-type: none;
  margin-top: 30px;
}

#navigation nav ul hr {
  border: 2px solid #e7eff6;
}

#navigation nav ul li {
  width: 90%;
  text-align: left;
  padding-left: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
}

#navigation nav ul li a {
  text-decoration: none;
  display: block;
  color: #4b86b4;
  font-size: 3em;
  font-weight: bold;
  width: 100%;
}

.cross {
  float: right;
  margin-right: 5%;
  margin-top: 15px;
}

.clearfix {
  clear: both;
}

