.project-preview {
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #e7eff6;
  border: solid 3px #e7eff6;
  text-decoration: none;
}

.project-preview .image-preview {
  display: flex;
  margin: 1rem;
  padding: 5px;
}

.project-preview .image-preview .image {
  flex: 0 0 60%;
  height: 300px;
  background-size: cover;
}

.project-preview .project-footer {
  display: flex;
  justify-content: space-between;
}

.blog-preview {
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #e7eff6;
  border: solid 3px #e7eff6;
  text-decoration: none;
  display: flex;
  flex-direction: column;
}

grid-overlay {
  position: absolute;
  left: 0;
  top: 0;
}

ul.counters {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  flex-direction: row;
}

ul.counters li {
  margin-right: 25px;
}

ul.counters .counter-number {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  margin-left: 10px;
}

ul.counters .heart {
  color: red;
}

ul.counters .comment {
  color: blue;
}

.counters-big {
  font-size: 28px;
}

.btn, .sm-btn, .lg-btn {
  background-color: #4b86b4;
  border-radius: 10px;
  text-decoration: none;
  color: white;
  border: 0px;
}

.sm-btn {
  padding: 6px 6px;
}

.lg-btn {
  padding: 6px 10px;
}

.btn-close-nav {
  color: #e7eff6;
  text-decoration: none;
  margin-right: 5px;
  margin-top: 5px;
}

.containerButtons {
  margin-top: 20px;
  margin-bottom: 20px;
}

h1 {
  font-size: 2.7em;
}

.containerLogin {
  width: 95%;
  display: block;
  margin: 0 auto;
}

.containerLogin .loginForm {
  width: 100%;
  margin-top: 50px;
}

.containerLogin div {
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
}

.containerLogin div label {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 300px;
  font-size: 1.5em;
  background-color: #63ace5;
  color: #e7eff6;
  text-align: left;
  text-transform: uppercase;
}

.containerLogin div input {
  display: block;
  margin: 0 auto;
  width: calc(100% - 4px);
  max-width: 296px;
  height: 25px;
  margin-top: 2px;
  font-family: "Montserrat";
}

.containerLogin div a {
  text-align: center;
  display: inline-block;
  width: 50%;
  max-width: 130px;
  margin: 0 auto;
  margin-top: 20px;
  text-transform: uppercase;
}

.containerLogin div input::-webkit-input-placeholder {
  font-family: 'Montserrat';
}

.containerLogin div input::-moz-placeholder {
  font-family: 'Montserrat';
}

.containerLogin div input:-ms-input-placeholder {
  font-family: 'Montserrat';
}

.containerLogin div input::placeholder {
  font-family: 'Montserrat';
}

form, .registerBtn, .error {
  width: 95%;
  max-width: 400px;
  display: block;
  margin: 0 auto;
}

.registerInput {
  width: 100%;
}

.registerInput input {
  width: 95%;
  max-width: 400px;
  margin: 0 auto;
  display: block;
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 1.5em;
  border: 1px solid #63ace5;
  color: #adcbe3;
  background-color: #e7eff6;
  padding-left: 5px;
}

.registerInput input::-webkit-input-placeholder {
  color: #adcbe3;
}

.registerInput input::-moz-placeholder {
  color: #adcbe3;
}

.registerInput input:-ms-input-placeholder {
  color: #adcbe3;
}

.registerInput input::placeholder {
  color: #adcbe3;
}

.registerBtn {
  margin-top: 20px;
  text-transform: uppercase;
}

.error {
  color: #ca331e;
}

.content-container {
  padding: 2rem;
  margin: 1rem 0;
  background-color: white;
}

.content-container .project-image {
  width: 100%;
  margin-top: 1rem;
}

.post-footer {
  padding: 1rem;
}

.contact-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  background-color: #e7eff6;
  padding: 2rem;
  margin: 1rem;
}

.contact-link .userIcon {
  color: lightgreen;
}

.profile-content {
  display: flex;
  flex-direction: column;
}

.profile-content .profileHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.blogpostContainer {
  display: block;
  margin: 0 auto;
  width: 95%;
}

.blogpostContainer h1 {
  color: #4b86b4;
  font-size: 2em;
  margin-left: 20px;
}

.blogpostContainer h3 {
  color: #2a4d69;
  font-size: 0.9em;
  font-family: 'Montserrat';
  margin-left: 20px;
  margin-top: -30px;
}

.blogpostContainer p, .blogpostContainer hr {
  max-width: 100%;
  width: calc(100%-4px);
  display: block;
  margin: 0 auto;
  text-align: left;
}

.blogpostContainer hr {
  border: 2px solid #e7eff6;
  margin-top: 3px;
  margin-bottom: 10px;
}

.blogpostContainer .author {
  margin-left: 35px;
  margin-top: 10px;
  font-size: 0.9em;
  padding-left: 5px;
  border-left: 2px solid #4b86b4;
}

.blogpostContainer form, .blogpostContainer .registerBtn, .blogpostContainer .error {
  margin-top: 20px;
  width: 100%;
  display: block;
  margin: 0 auto;
}

.blogpostContainer textarea {
  font-family: 'Montserrat';
  width: calc(100% - 4px);
  resize: none;
  height: 40px;
  border-radius: 10px;
  padding-top: 5px;
}

.blogpostContainer .lg-btn {
  margin-top: 40px;
}

.blogpostContainer input::-webkit-input-placeholder {
  font-family: 'Montserrat';
}

.blogpostContainer input::-moz-placeholder {
  font-family: 'Montserrat';
}

.blogpostContainer input:-ms-input-placeholder {
  font-family: 'Montserrat';
}

.blogpostContainer input::placeholder {
  font-family: 'Montserrat';
}

.contact-content {
  display: flex;
  flex-direction: column;
}

.contact-content .title__contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.socialMediaLinksContact {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

main img {
  width: 100%;
  background-color: black;
  margin-bottom: 15px;
}

main .profileBtns {
  width: 100%;
  display: block;
  margin: 0 auto;
}

main .profileBtns a {
  width: 50%;
}
